/** @format */

import axios from 'axios';

// https://api.tech41.de/yfm/v0/person/get/0x345879B60BF5ccDDd06BC91E49A6eBc4e93CfDAa

export async function sendVerifyEmail(email, code) {
  const POSTEMAIL = 'https://yfm_emailer.tech41.workers.dev/email';
  axios({
    method: 'post',
    url: POSTEMAIL,
    data: {
      to: email,
      code: code,
      tpl: 'validate',
    },
  });
}

export async function getPerson(pubkey) {
  const GETPERSONURL = 'https://api.tech41.de/yfm/v0/person/get/';
  const url = GETPERSONURL + pubkey;
  let res = await axios.get(url);
  return res.data;
}

export async function getPersonByUserName(username) {
  const GETPERSONURL = 'https://api.tech41.de/yfm/v0/person/getuser/';
  const url = GETPERSONURL + username;
  let res = await axios.get(url);
  return res.data;
}

export async function searchPerson(term) {
  const GETPERSONURL = 'https://api.tech41.de/yfm/v0/person/search/';
  const url = GETPERSONURL + term;
  let res = await axios.get(url);
  return res.data;
}

export async function SignVoucher(voucher) {
  const SIGNERURL = 'https://api.tech41.de/yfm/v0/signer';

  let res = await axios.post(SIGNERURL, {
    voucher: voucher,
  });
  return res.data;
}

export async function postPerson(person) {
  const POSTPERSONURL = 'https://api.tech41.de/yfm/v0/person/post/';
  axios
    .post(POSTPERSONURL, {
      person: person,
    })
    .then(function (response) {
      console.log(response);
    })
    .catch(function (error) {
      console.log(error);
    });
}

export async function sendSMS(mobile) {
  let res = await axios.get(
    'https://api.tech41.de/yfm/v0/sms/sendsms/' + mobile
  );
  return res.data.code;
}

export async function sendEmail(email) {
  alert(email);
  let res = await axios.get(
    'https://api.tech41.de/yfm/v0/email/sendemail' + email
  );
  alert(res.data.code);
  return res.data.code;
}
