/** @format */

import React, { useEffect, useState, updateState } from 'react';
import { useNavigate, useParams } from 'react-router-dom';
import QRCode from 'react-qr-code';
import {
  ExternalLinkIcon,
  StarIcon,
  PhoneIcon,
  EmailIcon,
  TimeIcon,
  DownloadIcon,
  InfoOutlineIcon,
  ViewIcon,
  CopyIcon,
  SunIcon,
} from '@chakra-ui/icons';
import {
  HStack,
  useClipboard,
  Table,
  Thead,
  Tbody,
  Tfoot,
  Tr,
  Th,
  Td,
  TableCaption,
  Text,
  Image,
  Heading,
  Container,
  Link,
  Button,
  Box,
  AlertTitle,
  AlertDescription,
  CloseButton,
  useLatestRef,
  VStack,
} from '@chakra-ui/react';

import { useMoralis } from 'react-moralis';

import * as PERSON from '../model/global';
import * as GLOBAL from '../model/global';
import * as HELPER from '../model/helper';
import * as WEB3 from '../io/web3_api';
import * as API from '../io/api';
import * as STATIC from '../model/static';

//******************************************************** */
export default function Show() {
  const [lastDid, setLastDid] = useState('');
  const [tokenid, setTokenid] = useState('');
  const [person, setPerson] = useState({});

  var { did } = useParams();

  const {
    Moralis,
    isAuthenticated,
    isAuthenticating,
    authError,
    authenticate,
    login,
    logout,
    user,
  } = useMoralis();
  //0x345879b60bf5ccddd06bc91e49a6ebc4e93cfdaa   Matd
  //0x58d22f24cd7fda155f9f7eefe9b32add46388ab9   Turtle
  //0x8db97c7cece249c2b98bdc0226cc4c2a57bf52fc   Cryptophine

  const navigate = useNavigate();
  const appState = GLOBAL.getAppState();

  useEffect(() => {
    if (lastDid == null || did != lastDid) {
      var pkey = HELPER.PubkeyFromDid(did);
      PERSON.loadYouByPubKey(pkey).then((person) => {
        if (person === undefined || Object.keys(person).length === 0) {
          return;
        }
        setLastDid(did);
        setPerson(person);
        if (person.did === undefined) {
          return;
        }

        if (person.did.length > 10) {
          var t = HELPER.TokenIdFromPubkey(person.pubkey);
          setTokenid(t);
        }
      });
    }
  });

  var avatarlink = '/avatar.html?url=' + person.avatar;

  const connect = (link) => {
    navigate('/connect/' + did);
  };

  const showlink = (link) => {
    if (link.startsWith('https')) {
      link = link.substring(8);
    }
    if (link.startsWith('http')) {
      link = link.substring(7);
    }
    if (link.length > 50) {
      link = link.substring(0, 50) + '...';
    }
    return link;
  };

  const { hasCopied, onCopy } = useClipboard(did);

  if (person === undefined || Object.keys(person).length === 0) {
    return (
      <main style={{ padding: '1rem 0' }}>
        <center>
          <Container width="100%"></Container>
          <h1>loading...</h1>
        </center>
      </main>
    );
  }

  return (
    <main style={{ padding: '1rem 0' }}>
      <Container width="100%">
        <Link onClick={onCopy}>
          <HStack>
            <Text fontSize="sm">{person.did}</Text>
            <CopyIcon />
          </HStack>
        </Link>
        <HStack>
          {person.metafile.length > 0 && (
            <Link href={person.metaverse} isExternal>
              <Text fontSize="sm">Metafile: {showlink(person.metafile)}</Text>
            </Link>
          )}
          {person.metafile.length == 0 && (
            <Text fontSize="sm">Metafile: NFT not minted</Text>
          )}
        </HStack>
        <br />
        <Heading>{person.username}</Heading>
        <br />
        <HStack>
          {person.img.length > 0 && (
            <Image
              boxSize="200px"
              src={person.img}
              objectFit="cover"
              alt={person.username}
            />
          )}

          <VStack>
            <Box boxSize="150px"></Box>
            <Button onClick={connect} disabled={!isAuthenticated}>
              Connect
            </Button>
          </VStack>
        </HStack>

        {person.avatar.length > 0 && (
          <HStack style={{ marginTop: '5px' }}>
            <Link color="teal.500" href={avatarlink} isExternal>
              Show Avatar
            </Link>
            <ExternalLinkIcon
              fontSize="xs"
              color="teal.500"
              style={{ marginBottom: '-1px', marginLeft: '2px' }}
            />{' '}
          </HStack>
        )}
        <br />
        <Heading>{person.jobtitle}</Heading>
        <Text fontSize="1xl">{person.teaser}</Text>
        <br />
        <Table variant="simple">
          <Tr>
            <Td>
              <Link href={person.ref0} isExternal>
                <ExternalLinkIcon color="blue.500" />
              </Link>
            </Td>
            <Td>
              <Link href={person.ref0} isExternal>
                <Text fontSize="1xl">{showlink(person.ref0)}</Text>
              </Link>
            </Td>
          </Tr>
          <Tr>
            <Td>
              <Link href={person.ref1} isExternal>
                <ExternalLinkIcon color="blue.500" />
              </Link>
            </Td>
            <Td>
              <Link href={person.ref1} isExternal>
                <Text fontSize="1xl">{showlink(person.ref1)}</Text>
              </Link>
            </Td>
          </Tr>
          <Tr>
            <Td>
              <Link href={person.ref2} isExternal>
                <ExternalLinkIcon color="blue.500" />
              </Link>
            </Td>
            <Td>
              <Link href={person.ref2} isExternal>
                <Text fontSize="1xl">{showlink(person.ref2)}</Text>
              </Link>
            </Td>
          </Tr>
          <Tr>
            <Td>
              <Link href={person.location} isExternal>
                <SunIcon color="blue.500" />
              </Link>
            </Td>
            <Td>
              <Link href={person.location} isExternal>
                <Text fontSize="1xl">{showlink(person.location)}</Text>
              </Link>
            </Td>
          </Tr>
          <Tr>
            <Td>
              <Link href={person.metaverse} isExternal>
                <ViewIcon color="blue.500" />
              </Link>
            </Td>
            <Td>
              <Link href={person.metaverse} isExternal>
                <Text fontSize="1xl">{showlink(person.metaverse)}</Text>
              </Link>
            </Td>
          </Tr>
          <Tr>
            <Td colSpan="2">
              <Text fontSize="1xl" color="teal.500">
                + details, only visible to contacts confirmed by you
              </Text>
            </Td>
          </Tr>
          <Tr>
            <Td>
              <InfoOutlineIcon color="blue.500" />
            </Td>
            <Td>
              <Text fontSize="1xl" color="teal.500">
                {person.gname} {person.sname}
              </Text>
            </Td>
          </Tr>
          <Tr>
            <Td>
              <EmailIcon color="blue.500" />
            </Td>
            <Td>
              <Text fontSize="1xl" color="teal.500">
                {person.email}
              </Text>
            </Td>
          </Tr>
          <Tr>
            <Td>
              <PhoneIcon color="blue.500" />
            </Td>
            <Td>
              <Text fontSize="1xl" color="teal.500">
                {person.mobile}
              </Text>
            </Td>
          </Tr>
          <Tr>
            <Td>
              <StarIcon color="blue.500" />
            </Td>
            <Td>
              <Text color="teal.500" fontSize="1xl">
                {person.tags}
              </Text>
            </Td>
          </Tr>
          <Tr>
            <Td>
              <StarIcon color="blue.500" />
            </Td>
            <Td>
              <Text color="teal.500" fontSize="1xl">
                {person.metafile}
              </Text>
            </Td>
          </Tr>
          <Tr>
            <Td>
              <DownloadIcon color="blue.500" />
            </Td>
            <Td color="teal.500">VCF card | VCF card+</Td>
          </Tr>
          <Tr>
            <Td>
              <Text color="blue.500" fontSize="sm">
                QR Link
              </Text>
            </Td>
            <Td>
              <QRCode
                level="H"
                size="165"
                title="Youfoundme DID"
                fgColor="#FFFFFF"
                bgColor="#000000"
                value={'https://app.youfoundme.io/#/show/' + did}
              />
            </Td>
          </Tr>
        </Table>
      </Container>
    </main>
  );
}
